* {
  box-sizing:border-box;
}
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.App {
  display: flex;
  flex-direction: column;
  height:100%;
  font-family: 'Encode Sans Condensed';
}
body,html,#root {
  height:100%;
}
input[type="submit"] {
  cursor:pointer;
}
header {
  background: #56409B;
  text-align: center;
  padding: 20px 10px;
  display:flex;
  justify-content: center;
  align-items: center;
  box-shadow:0px 5px 7px rgba(0,0,0,0.3);
  z-index: 9999;
  
  zoom:50%;
}
header .menu {
  position: absolute;
  padding-right:80px;
  right:0px;
  cursor: pointer;
}
.main-menu {
  position: absolute;
  top: 94px;
  background: #56409B;
  color: #fff;
  font-size: 33px;
  width: 418px;
  right: 0px;
  display:flex;
  flex-direction: column;
  text-align:left;
  border-top:1px solid #fff;
}
.main-menu .menu-item {
  padding: 21px 32px;
  border-bottom:1px solid #fff;
  display: flex;
  position: relative;
}
.main-menu .menu-item:hover {
  background:#93E593;
}
.theme-color {
  width:40px;
  height:40px;
  background:#fff;
  border-radius: 20px;
  border:2px solid #000;
  margin-right:30px;
}
.theme-color.dark {
  background:#000;
  border:2px solid #fff;
}
.font-size {
  display: flex;
  top: 0px;
  margin-top: -18px;
  align-items: baseline;
  margin-right: 30px;
}
.font-size .large {
  font-size:50px;
}
.font-size .small {
  margin-left:-4px;
  font-size:33px;
}
.body {
  display:flex;
  height:100%;
}
.sidebar {
  display:flex;
  flex-direction: column;
  height:100%;
  width:129px;
  background:#EBE9E3;
  z-index: 9998;
  zoom:50%;
}
.menu-icon .menu-image-wrapper {
  width:100%;
  cursor: pointer;
  
  padding:31px;
}
.menu-icon img {
  width:59px;
  margin:0 auto;
  pointer-events: none;
  user-select: none;
}
.menu-icon {
  border-bottom:1px solid #fff;
  width:100%;
  text-align: center;
  position: relative;
}
.menu-icon:hover {
  background:#7B6CBC;
}
.menu-icon label {
  display:none;
  top:0px;
  left:120px;
  width:426px;
  height:100%;
  position:absolute;
  background:#56409B;
  color:#fff;
  font-size:35px;
  justify-content: center;
  align-items: center;
}
.menu-icon label .arrow-down {
  transform:rotate(180deg) scale(1,0.6);
  margin-right:20px;
  font-size:64px;
  margin-bottom:6px;
}
.menu-icon:hover img {
  cursor: pointer;
  filter: brightness(0) invert(1);
}
.menu-icon.active {
  background:#56409B;
}
.menu-icon.active label {
  display:flex;
  cursor: pointer;
}
.menu-icon.active img {
  filter: brightness(0) invert(1);
}
.main {
  width:100%;
  background:#fff;
  color:#000;
  margin-left:210px;
}
.body.dark {
  background:#000;
}
.main.dark {
  background:#000;
  color:#fff;
}
.screen {
  display:flex;
  justify-content: center;
  align-items: center;
  width:100%;
  height:100%;
  padding:40px;
  max-height: 740px;
}
.landing-icon {
  color:#fff;
  background:#56409B;
  text-align: center;
  font-size:36px;
  display: flex;
  justify-content: center;
  align-items: center;
  width:479px;
  height:479px;
  flex-direction: column;
  margin:0px 28px;
  cursor: pointer;
}
.landing-icon:hover {
  background:#93E593;
}
.landing-icon img {
  max-width:179px;
  margin-bottom:84px;
  height:130px;
  object-fit: contain;
}
.footer {
  color:#fff;
  display: flex;
  background:#56409B;
  justify-content: space-around;
  font-size:23px;
  padding:5px 0px;
  zoom:50%;
}



