  font-size:50px;
  display:flex;
  flex-wrap: wrap;
  text-align: center;
  max-width:714px;
  margin:0 auto;
}
form.search-form label {
  width:100%;
  margin-bottom:40px;
}
.screen.screen-2, .screen.screen-3,.screen.screen-4 {
  flex-direction: column;
  overflow-y: scroll;
  max-height:870px;
  justify-content: start;
  align-items: flex-start;
  padding-top:40px;
}
.screen.screen-2 {
  max-height:870px;
  justify-content: start;
  align-items: flex-start;
  padding-top:40px;
}
.gene-name {
  font-size:42px;
  font-weight:bold;
  text-align: left;
  width: 100%;
  margin-bottom:40px;
}
.gene-location {
  text-align: left;
  width: 100%;
