.pam-box {
    display: flex;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background:rgba(0,0,0,0.2);
}
.pam-box h3 {
  font-size: 68px;
  font-weight: 600;
  color:#56409B;
}
.pam-box h4 {
  font-size:48px;
  font-weight: 600;
}
.pam-box .pam-wrapper {
  font-size:36px;
  background:#fff;
  border:40px solid #56409B;
  padding:40px;
  max-height: 161vh;
  overflow: hidden;
  overflow-y: scroll;
}
.amino-table {
  display: flex;
}
.amino-table h4 {
  padding:0px 20px;
}
.amino-table .chart {
  display:flex;
  flex-direction: column;
}
.amino-table .header {
  display:flex;
  justify-content: center;
}
.amino-table div {
  align-items: center;
  display: flex;
}
.amino-table .header .cell {
  height:100px;
  display:flex;
  justify-content: center;
  align-items: center;
}
.amino-table .cell {
  width: 296px;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  border: 10px solid #56409B;
  font-size: 48px;
  font-weight: 700;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-bottom:0px;
  border-left:0px;
  cursor: pointer;
}
.amino-table .cell .cell-box > div {
    flex-wrap:wrap;
}
.amino-table .cell .mutation:hover {
  background:#BCAD8A;
}
.amino-table .row .cell:first-of-type,.amino-table .row .cell:last-of-type {
  width:100px;
}
.amino-table .row:last-of-type {
  border-bottom:10px solid #56409B;
}
.amino-table .row .cell:first-of-type,.amino-table .header .cell:first-of-type {
  border-left:10px solid #56409B;
}
.amino-table .cell-box {
  display: flex;
  padding-top:10px;
  padding-bottom:10px;
  width:100%;
}
.amino-table .cell .cell-right {
  height:100%;
  border-left: 10px solid #56409B;
  margin-left: 24px;
  padding-left: 10px;
  height: 100%;
  color:#56409B;
  width:100%;
  justify-content: center;
}
.pam-wrapper form {
  margin-top:40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pam-wrapper form input {
  width: 150px;
    height: 94px;
    font-size: 48px;
    padding: 20px;
}
.pam-wrapper form input[type="submit"] {
  width:120px;
  background:#BCAD8A;
  border:0px;
  color:#fff;
  font-size:24px;
  border-radius:0px 8px 8px 0px;
}
.amino-table .cell .cell-right.stop {
  border-left: 0px;
  color:#FF668E;
}
.amino-table .cell .cell-right.met {
  border-left: 0px;
  color:#93E593;
}
.amino-table .chart .header, .amino-table .chart .row {
  display:flex;
}
.homology-list {
  max-height: 861px;
  top: 124px;
  overflow-y: scroll;
  overflow-x: hidden;


}
.homology-label {
  background:#56409B;
  color:#fff;
  padding:30px 0px;
  font-size:32px;
}
.download-list {
  overflow: hidden;
  overflow-y: scroll;
  height: 100%;
  min-height: 540px;
}
.download-list select {
  font-size:24px;
  width:90%;
  margin:0 5%;
}
.download-list .download-label {
  padding: 40px 0px 20px;
  font-size: 36px;
}
.download-list button {
  background: #56409B;
  color:#fff;
  font-size: 28px;
  border:0;
  border-radius: 10px;
  margin:20px 0px;
  padding:10px 20px;
}
.blink {
  -webkit-animation: blink 3s infinite both;
          animation: blink 3s infinite both;
}

@-webkit-keyframes blink {
0%,
50%,
100% {
  opacity: 1;
}
25%,
75% {
  opacity: 0;
}
}
@keyframes blink {
0%,
50%,
100% {
  opacity: 1;
}
25%,
75% {
  opacity: 0;
}
}